import AclGuard from "components/AclGuard/AclGuard";
import AutoComplete from "components/AutoComplete/AutoComplete";
import ButtonBox from "components/ButtonBox/ButtonBox";
import DocumentUploader, {
  DOC_UPLOADER_MODE,
  IDocumentUploaderRef,
} from "components/DocumentUploader/DocumentUploader";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import IconButton, { BUTTON_TYPES } from "components/IconButton/IconButton";
import InputBox from "components/InputBox/InputBox";
import InputError from "components/InputError/InputError";
import ReadOnlyTable, { IReadonlyTableRef } from "components/ReadOnlyTable/ReadOnlyTable";
import SkeletonCustom from "components/Skeleton/Skeleton";
import SystemDatePicker from "components/SystemDatePicker/SystemDatePicker";
import Text from "components/Text/Text";
import TextData from "components/TextData/TextData";
import ToastCustom from "components/Toast";
import WarningDialog from "components/WarningDialog/WarningDialog";
import { RouteProps, withRouter } from "hoc/withRouter";
import { useTranslateText } from "hook/CustomHooks";
import { numberFormatter } from "locale";
import { Column } from "primereact/column";
import { useEffect, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import DepositAccountService from "services/transaction/DDE/DepositAccountService";
import FeeService from "services/transaction/FeeService";
import SimpleReactValidator from "simple-react-validator";
import {
  LINK_FEES_DEPOSIT,
  LINK_FEES_DEPOSIT_CREATE_EDIT_FROM_RECONCILIATION,
  LINK_RECONCILIATION,
} from "transactionRoutes";
import {
  convertToIndianNumbering,
  formatDate,
  getStandardValidator,
  messageReplace,
  parseStandardAPIErrorMessage,
} from "utils/CommonUtil";
import {
  BUTTON_TYPE,
  DOCUMENT_CATEGORY,
  FEE_MODE,
  FEE_MODE_LABEL,
  FEE_RECEIPT_STATUS,
  INPUT_STYLE_TYPE,
  LOAN_SECTION_KEY,
  PAGE_MODE,
  STATIC_MASTER_TYPE_TOAST_TEXT,
  TOAST_STYLE_TYPE,
} from "utils/Constant";
import { getNumber } from "views/transaction/PageCAM/Helper";
import {
  IDropDownOptions,
  prepareAttributes,
  prepareDocSections,
  structLoanDocumentsFromAPIResponse,
} from "views/transaction/PageDDE/Login/Helper";
import { IFeeCollectionReceiptRowData } from "../PageReceipts/PageReceipts";

interface IAutocomplete {
  name: string;
  uid: string;
}

interface IPageObj {
  sahayyaBankAccount: {
    name: string;
    uid: string;
    branch: string;
    accountNumber?: string;
  };
  loan: IAutocomplete;
  tableData: IFeeCollectionReceiptRowData[];
  dateOfDeposits: string;
  deleteReceipt: {
    showPopUp: boolean;
    uid: string;
  };
  dropdownOptions: {
    instrumentType: IDropDownOptions[];
    // instrumentType:IDropDownOptions[];
  };
  depositAmount: number | string;
  depositAmountInWords: string;
  savedDocuments: any[];
  pageMode: string;
  isLoading: boolean;
}

const initialPageObj: IPageObj = {
  sahayyaBankAccount: {
    uid: "",
    name: "",
    branch: "",
    accountNumber: "",
  },
  loan: {
    uid: "",
    name: "",
  },
  tableData: [],
  dateOfDeposits: "",
  dropdownOptions: {
    instrumentType: [],
  },
  deleteReceipt: {
    showPopUp: false,
    uid: "",
  },
  depositAmount: "",
  depositAmountInWords: "",
  savedDocuments: [],
  pageMode: PAGE_MODE.EDIT,
  isLoading: true,
};

const PageDepositCreateOrEdit = (props: RouteProps) => {
  const params: { depositUid?: string } = useParams();
  /*
    -------------------------------------------------------
      State
    -------------------------------------------------------
    */

  const [pageObj, setPageObj] = useState<IPageObj>({
    ...initialPageObj,
    pageMode: params?.depositUid ? PAGE_MODE.VIEW : PAGE_MODE.EDIT,
  });
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [toastMessage, setToastMessage] = useState("");
  const [toastMessageVariant, setToastMessageVariant] = useState(TOAST_STYLE_TYPE.PRIMARY);
  const [triggerToast, setTriggerToast] = useState(false);
  const [suggestionsTemplate, setSuggestionsTemplate] = useState<any>([{ name: "" }]);
  const [, forceUpdate] = useState<boolean>(false);

  const dataTableRef = useRef<IReadonlyTableRef>(null);
  const receiptDeleteMenuRef = useRef<any>(null);
  const toastMessageRef = useRef(null);

  const simpleValidator = useRef(
    new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    })
  );
  const documentUploaderRef = useRef<IDocumentUploaderRef | null>(null);

  // --------------------------- global variables ---------------------- //

  /*
     -------------------------------------------------------
       useEffect
     -------------------------------------------------------
   */
  useEffect(() => {
    dataTableRef?.current?.search();
    // getInstrumentType();
  }, []);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);

  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore: will be fixed in future
      toastMessageRef?.current?.showFunction();
      setToastMessage("");
    }
  }, [toastMessage, toastMessageVariant, triggerToast]);

  useEffect(() => {
    setPageObj((prevPageObj) => {
      const updatedPageObj = { ...prevPageObj };
      let depositAmount = 0;
      if (Array.isArray(updatedPageObj.tableData)) {
        for (const row of updatedPageObj.tableData) {
          depositAmount += getNumber(row.amount);
        }
        updatedPageObj.depositAmount = depositAmount;
        updatedPageObj.depositAmountInWords = convertToIndianNumbering(depositAmount);
      }
      return updatedPageObj;
    });
  }, [pageObj?.tableData]);

  useEffect(() => {
    if (params?.depositUid) {
      getDepositDetails();
    }
  }, [params?.depositUid]);

  const getDepositDetails = async () => {
    setPageObj((prevObj) => ({
      ...prevObj,
      isLoading: true,
    }));
    try {
      const res: any = await FeeService.getOneFeeCollectionDeposit(params?.depositUid);
      const receiptRecords: any[] = [];
      if (res?.data?.depositReceipts) {
        for (const record of res.data.depositReceipts) {
          if (record?.receipt) {
            receiptRecords.push(record?.receipt);
          }
        }
      }
      setPageObj((prevObj) => ({
        ...prevObj,
        dateOfDeposits: res?.data?.depositDate,
        depositAmount: res?.data?.totalAmount,
        totalAmountInWords: res?.data?.totalAmountInWords,
        savedDocuments: structLoanDocumentsFromAPIResponse(res?.data?.loanDocuments),
        tableData: receiptRecords,
        sahayyaBankAccount: {
          ...prevObj.sahayyaBankAccount,
          name: res?.data?.depositAccount?.holderName,
          branch: res?.data?.depositAccount?.ifsc?.branch,
          accountNumber: res?.data?.depositAccount?.accountNumber,
        },
        isLoading: false,
      }));
    } catch (err) {
      console.log(err);
      setErrorMessage(parseStandardAPIErrorMessage(err));
      setPageObj((prevObj) => ({
        ...prevObj,
        isLoading: false,
      }));
    }
  };

  /*
    -------------------------------------------------------
      Service Calls
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Handlers
    -------------------------------------------------------
  */

  const handleChange = (event: any) => {
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      [event.target.name]: event.target.value,
    }));
  };

  const handleAutoCompleteBankChange = (event: any) => {
    const { value } = event.target;
    if (typeof value === "object") {
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        [event.target.name]: {
          name: value?.bank?.name,
          uid: value.uid,
          branch: value?.ifsc?.branch,
          accountNumber: value?.accountNumber,
        },
      }));
    } else {
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        [event.target.name]: { name: value, uid: "", branch: "", accountNumber: "" },
      }));
    }
  };

  const handleAutoCompleteReceiptChange =async (event: any) => {
    const { value } = event.target;

    if (typeof value === "object") {
      let res;
      try {
         res =await  FeeService.getFeeCollectionReceipt(0, 1000, `&loan.uid=${value?.uid}&smInstrumentType:nq=${FEE_MODE?.UPI}&status=${FEE_RECEIPT_STATUS.PENDING_DEPOSIT}`)
      } catch (err) {
         setErrorMessage(parseStandardAPIErrorMessage(err))
      }
     
      // let
      let receipts:any[] =[]
      if(res?.data?.results){
        receipts=[...res.data.results]
      }
      setPageObj((prevPageObj) => {
        const updatedPageObj = { ...prevPageObj };
        const rowData = event.target.value;
        updatedPageObj.tableData = [...updatedPageObj.tableData,...receipts ];
        updatedPageObj.loan.name = "";
        return updatedPageObj;
      });
    } else {
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        loan: { ...prevPageObj.loan, name: value },
      }));
    }
  };

  /* 
    --------------------------------------------------------
    Validators:
    --------------------------------------------------------
  */

  const validate = (e: any) => {
    simpleValidator?.current?.showMessageFor(e?.target?.name);
  };

  const validateForm = () => {
    let isValid = false;
    isValid = simpleValidator?.current?.allValid() ?? false;
    if (isValid) {
      return isValid;
    } else {
      forceUpdate((prev) => !prev);
      simpleValidator?.current?.showMessages();
      forceUpdate((prev) => !prev);
      return false;
    }
  };

  /*
    -------------------------------------------------------
      Button Click Handlers
    -------------------------------------------------------
  */

  const onResetWarningPop = () => {
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      deleteReceipt: {
        ...prevPageObj.deleteReceipt,
        showPopUp: false,
        uid: "",
      },
    }));
  };

  const onDeleteReceiptConfirm = async () => {
    // call delete api
    try {
      await FeeService.deleteDdeReceipt(pageObj.deleteReceipt.uid);
      setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
      setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
      setTriggerToast(!triggerToast);
      dataTableRef?.current?.search();
      onResetWarningPop();
      // addReceiptRef.current?.onCloseAndClearData();
    } catch (err) {
      console.log(err);
      setErrorMessage(parseStandardAPIErrorMessage(err));
    }
  };

  const onSave = async () => {
    const uploaderData = documentUploaderRef?.current?.getUploadedData();
    // if(!uploaderData){
    //   documentUploaderRef?.current?.
    // }
    if (!validateForm() || !uploaderData) {
      return;
    }
    const payload = enrichForSave(uploaderData);
    if (payload) {
      try {
        await FeeService.createFeeCollectionDeposit(payload);
        setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
        setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
        setTriggerToast(!triggerToast);
        props.navigate(LINK_FEES_DEPOSIT);
      } catch (err) {
        console.log(err);
        setErrorMessage(parseStandardAPIErrorMessage(err));
      }
    }
    console.log("on save");
  };

  const onEdit = () => {
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      pageMode: PAGE_MODE.EDIT,
    }));
  };

  const onCancel = () => {
    if (props?.location?.pathname.includes(LINK_FEES_DEPOSIT_CREATE_EDIT_FROM_RECONCILIATION)) {
      props.navigate(LINK_FEES_DEPOSIT);
      props.navigate(LINK_RECONCILIATION);
    } else {
      props.navigate(LINK_FEES_DEPOSIT);
    }
  };

  /*
    -------------------------------------------------------
      Helper Functions
    -------------------------------------------------------
  */
  const enrichForSave = (uploaderData: any) => {
    const depositReceipts: any[] = [];
    pageObj.tableData.forEach((rowData) => {
      depositReceipts.push({
        loan: {
          uid: rowData?.loan?.uid,
        },
        receipt: {
          uid: rowData?.uid,
        },
        deposit: null,
      });
    });
    return {
      totalAmount: pageObj?.depositAmount,
      totalAmountInWords: pageObj?.depositAmountInWords,
      depositDate: pageObj?.dateOfDeposits,
      depositAccount: {
        uid: pageObj?.sahayyaBankAccount?.uid,
      },
      loanDocuments: uploaderData?.documentUid
        ? [
            {
              uid: uploaderData?.uid,
              docType: {
                uid: uploaderData?.documentUid,
              },
              isActive: uploaderData?.isActive,
              docNo: uploaderData?.[`${uploaderData.primaryKeyAttributeName}`] ?? "",
              dob: uploaderData.dob ? uploaderData?.unsavedDocuments.dob : null,
              loanSectionKey: LOAN_SECTION_KEY.RECEIPT,
              docCategory: uploaderData?.documentCategory,
              attributes: prepareAttributes(uploaderData),
              documentSections: prepareDocSections(uploaderData?.documentSectionUrls),
            },
          ]
        : [],
      depositReceipts: depositReceipts,
    };
  };
  const resetValidator = () => {
    simpleValidator.current = new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    });
    simpleValidator?.current?.hideMessages();
  };

  const onDeleteRow = (receiptUid: string) => {
    setPageObj((prevObj) => {
      const updatedObj = { ...prevObj };
      updatedObj.tableData = updatedObj.tableData.filter((receipt) => receipt.uid !== receiptUid);
      return updatedObj;
    });
    // if (receiptUid) {
    //   setPageObj((prevObj) => ({
    //     ...prevObj,
    //     deleteReceipt: {
    //       ...prevObj.deleteReceipt,
    //       showPopUp: true,
    //       uid: receiptUid,
    //     },
    //   }));
    // }
  };

  const searchAutoComplete = async (
    event: any,
    options: {
      service: any;
      field: string;
      search: string;
    }
  ) => {
    let suggestionsTemplate = [];
    await options?.service?.(0, 10, event.query).then((res: any) => {
      if (res.data.results) {
        if (res?.status === 200) {
          suggestionsTemplate = [...res.data.results];
          // setIsLoading(false);
        }
      }
    });
    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "" });
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  const searchAutoCompleteReceipt = async (event: any) => {
    let suggestionsTemplate = [];
    //  event.query
    // if(sourcingDetails?.sourceAndBranch?.branch?.uid){
    const digitsRegex = /^[0-9]$/;
    let search = "";
    if (event?.query?.toLowerCase()?.includes("lan") || digitsRegex?.test(event?.query)) {
      search = `&lanId:lk=${event?.query}`;
    } else {
      search = `&applicantName:lk=${event?.query}`;
    }
    try {
      await FeeService.getLoans(0, 100, search).then((res) => {
        if (res.status === 200 && res?.data?.results) {
          suggestionsTemplate = [...res.data.results];

          // setIsLoading(false);
        }
      });
    } catch (error) {
      console.log(error);
    }

    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "", uid: "" });
    }

    setSuggestionsTemplate(suggestionsTemplate);
    // }
  };

  /*
    -------------------------------------------------------
      Sectional Rendering
    -------------------------------------------------------
  */

  const itemTemplateSahayyaBankAccount = (item: any) => {
    if (item?.uid === undefined || item?.uid === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39 pointer-events-none cursor-default">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item?.bank?.name}`} />
      </div>
    );
  };

  const itemTemplateReceipt = (item: any) => {
    if (item?.uid === undefined || item?.uid === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39 pointer-events-none cursor-default">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text
          className="text-primary-labelColor"
          label={`${item?.lanId} | ${item?.applicantName} | ${
            item?.branch
          }`}
        />
      </div>
    );
  };

  const renderLoanApplicationNo = (rowData: IFeeCollectionReceiptRowData) => {
    return <Text className=" text-xs text-secondary-labelColor " label={rowData?.loan?.lanId} />;
  };

  const renderApplicantName = (rowData: IFeeCollectionReceiptRowData) => {
    return (
      <Text className=" text-xs text-secondary-labelColor " label={rowData?.loan?.applicantName} />
    );
  };

  const renderBranch = (rowData: IFeeCollectionReceiptRowData) => {
    return (
      <Text className=" text-xs text-secondary-labelColor " label={rowData?.loan?.branch?.name} />
    );
  };

  const renderInstrumentType = (rowData: IFeeCollectionReceiptRowData) => {
    return (
      <Text
        className=" text-xs text-secondary-labelColor "
        label={FEE_MODE_LABEL?.[rowData?.smInstrumentType] ?? ""}
      />
    );
  };

  const renderInstrumentDate = (rowData: IFeeCollectionReceiptRowData) => {
    return (
      <Text
        className=" text-xs text-secondary-labelColor "
        label={formatDate(rowData?.instrumentDate)}
      />
    );
  };

  const renderUtrOrCheckNo = (rowData: IFeeCollectionReceiptRowData) => {
    return <Text className=" text-xs text-secondary-labelColor " label={rowData?.txnRefNo} />;
  };

  const renderAmount = (rowData: IFeeCollectionReceiptRowData) => {
    //  let amount =params?.depositUid ? rowData?. :rowData?.amount;
    return (
      <Text
        className=" text-xs text-secondary-labelColor "
        label={numberFormatter(rowData?.amount, "en-IN", 0)}
      />
    );
  };

  const renderCollectedBy = (rowData: IFeeCollectionReceiptRowData) => {
    return (
      <Text className=" text-xs text-secondary-labelColor " label={rowData?.createdByUserName} />
    );
  };

  const renderUpdatedOn = (rowData: IFeeCollectionReceiptRowData) => {
    return (
      <Text
        className=" text-xs text-secondary-labelColor "
        label={formatDate(rowData?.updatedAtSystemDate)}
      />
    );
  };

  const renderActions = (rowData: IFeeCollectionReceiptRowData) => {
    return (
      <div className="grid grid-cols-2">
        <div className="">
          <IconButton
            buttonType={BUTTON_TYPES.DELETE}
            onClick={() => onDeleteRow(rowData?.uid)}
            // disabled={pageMode === PAGE_MODE.VIEW}
            tooltipOptions={{
              position: "left",
              className: "text-xs font-Roboto bg-black text-white !p-1 rounded-md ",
              showDelay: "1000,",
              hideDelay: "300",
            }}
          />
        </div>
      </div>
    );
  };

  const renderTable = () => {
    return (
      <div className="col-span-4">
        <ReadOnlyTable
          // standardDataFormatPromise={FeeService.getFeeList}
          value={pageObj.tableData}
          dataKey="uid"
          ref={dataTableRef}
          className={`  make-deposit-table `}
        >
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={useTranslateText("label.fees.loanApplicationNo(s)")}
            body={renderLoanApplicationNo}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6 "
            header={useTranslateText("label.fees.applicantName")}
            body={renderApplicantName}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs w-1/12  p-1 h-12 pl-6"
            header={useTranslateText("label.fees.branch")}
            body={renderBranch}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
            header={useTranslateText("label.fees.instrumentType")}
            body={renderInstrumentType}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={useTranslateText("label.fees.instrumentDate")}
            body={renderInstrumentDate}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={useTranslateText("label.fees.utr/Chq.No.")}
            body={renderUtrOrCheckNo}
          />
          <Column
            className="text-primary-ptext-1000 text-right text-xs  p-1 h-12 pl-6"
            header={useTranslateText("label.fees.amount")}
            body={renderAmount}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={useTranslateText("label.fees.collectedBy")}
            body={renderCollectedBy}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
            header={useTranslateText("label.fees.updatedOn")}
            body={renderUpdatedOn}
          />
          <Column
            className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
            header={useTranslateText("label.fees.action")}
            body={renderActions}
          />
        </ReadOnlyTable>
        <InputError
          validation={simpleValidator?.current?.message(
            `receipts`,
            pageObj?.tableData?.length,
            "arrayMinOneReceipt"
          )}
        />
      </div>
    );
  };

  const renderContent = () => {
    return (
      <div className=" grid grid-cols-4 gap-3">
        <div>
          <AutoComplete
            label="label.fees.sahayyaBankAccount*"
            name="sahayyaBankAccount"
            field="name"
            value={pageObj?.sahayyaBankAccount?.name}
            variant={INPUT_STYLE_TYPE.PRIMARY}
            suggestions={suggestionsTemplate}
            completeMethod={(e: any) =>
              searchAutoComplete(e, {
                field: "",
                search: "",
                service: DepositAccountService.getBankAccount,
              })
            }
            onBlur={validate}
            validation={simpleValidator?.current?.message(
              `sahayyaBankAccount`,
              pageObj?.sahayyaBankAccount?.uid,
              "mandatory"
            )}
            onChange={handleAutoCompleteBankChange}
            itemTemplate={itemTemplateSahayyaBankAccount}
          />
        </div>
        <div>
          <InputBox
            label="label.fees.accountBranch"
            name=""
            value={pageObj?.sahayyaBankAccount?.branch}
            variant={INPUT_STYLE_TYPE.SECONDARY}
            disabled
          />
        </div>
        <div>
          <InputBox
            label="label.fees.branchAccountNumber"
            name=""
            value={pageObj?.sahayyaBankAccount?.accountNumber}
            variant={INPUT_STYLE_TYPE.SECONDARY}
            disabled
          />
        </div>
        <div></div>
        <div className="col-span-4">
          <AutoComplete
            label="label.fees.applicantNameOrApplicationNo"
            name="receipt"
            field="name"
            value={pageObj?.loan?.name}
            variant={INPUT_STYLE_TYPE.PRIMARY}
            suggestions={suggestionsTemplate}
            completeMethod={searchAutoCompleteReceipt}
            // onBlur={validate}
            // validation={simpleValidator?.current?.message(
            //   `applicant`,
            //   pageObj?.applicant?.uid,
            //   "mandatory"
            // )}
            onChange={handleAutoCompleteReceiptChange}
            itemTemplate={itemTemplateReceipt}
          />
        </div>
        {renderTable()}
        <div>
          <TextData
            label="label.fees.depositAmount(₹)*"
            value={`${numberFormatter(getNumber(pageObj?.depositAmount), "en-IN", 0)}`}
          />
        </div>
        <div>
          <TextData label="label.fees.amountInWords" value={`${pageObj?.depositAmountInWords}`} />
        </div>
        <div></div>
        <div className="grid-group">
          <SystemDatePicker
            label={"label.fees.dateOfDeposit*"}
            name="dateOfDeposits"
            onChange={handleChange}
            value={pageObj?.dateOfDeposits ? new Date(pageObj?.dateOfDeposits) : null}
            variant={INPUT_STYLE_TYPE.SECONDARY}
            onBlur={validate}
            validation={simpleValidator?.current?.message(
              `dateOfDeposits`,
              pageObj?.dateOfDeposits,
              "mandatory"
            )}
          />
        </div>
        <div className="col-span-4">
          <div className="col-span-4">
            <DocumentUploader
              ref={documentUploaderRef}
              savedDocument={pageObj.savedDocuments?.[0] ? pageObj.savedDocuments?.[0] : {}}
              mode={params?.depositUid ? DOC_UPLOADER_MODE.VIEW : DOC_UPLOADER_MODE.EDIT}
              queryParamForDocList={DOCUMENT_CATEGORY.DOCUMENT_CATEGORY_FEE_DEPOSIT}
            />
          </div>
        </div>
      </div>
    );
  };

  /*
    -------------------------------------------------------
    Main Rendering
    -------------------------------------------------------
  */

  return (
    <div className="p-3 rounded-masterDataPageRadius bg-white flex flex-col gap-3 overflow-scroll h-screen">
      <div className="flex w-full justify-between items-center">
        <Text
          label={params?.depositUid ? "label.fees.editDeposit" : "label.fees.makeDeposit"}
          className="text-primary-ptext-1000 text-h2 font-bold flex"
        />

        <div className="flex gap-10">
          <ButtonBox
            buttonType={BUTTON_TYPE.CANCEL_OUTLINE}
            className="create-button"
            onClick={onCancel}
          />
          {pageObj.pageMode === PAGE_MODE.VIEW ? null : (
            // <ButtonBox buttonType={BUTTON_TYPE.EDIT_OUTLINE} onClick={onEdit} />
            <ButtonBox buttonType={BUTTON_TYPE.MAKE_DEPOSIT} onClickWithLoader={onSave} />
          )}
        </div>
      </div>

      <AclGuard override={{ read: pageObj.pageMode === PAGE_MODE.VIEW }}>
        {pageObj.isLoading === undefined ? (
          <SkeletonCustom height="19.375rem" width="100%" borderRadius="8px" animation="wave" />
        ) : (
          // "Hi"
          renderContent()
        )}
      </AclGuard>

      <WarningDialog
        visible={pageObj?.deleteReceipt?.showPopUp}
        className="w-76 !px-14"
        title={"heading.dde.fees.receiptDelete"}
        warningMessage={`description.dde.fees.receiptDelete`}
        onCancel={onResetWarningPop}
        onConfirm={() => {
          return onDeleteReceiptConfirm();
        }}
        onHide={() => {
          //
        }}
      />
      <ToastCustom
        position={"bottom-right"}
        ref={toastMessageRef}
        toastText={toastMessage}
        variant={toastMessageVariant}
      />
      <ErrorDialog
        onHide={() => {
          setShowDialogAPIError(false);
          setErrorMessage("");
        }}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
    </div>
  );
};

export default withRouter(PageDepositCreateOrEdit);
